.TeamPage{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr 1fr;
    grid-column-gap: 10px;
    grid-row-gap: 10px;

    .team-name {
        grid-column: span 3;
        justify-self: start;
        align-self: end;
    }

    .team-name-disp {
        font-size: 4rem;
        margin-bottom: 20px;
    }

    .win-loss {
        justify-self: center;
        font-weight: bold;
        text-align: center;
        padding: 20px;
    }

    .match-detail {
        grid-column: span 4;
    }

    .more-link {
        align-self: center;
        justify-self:  center;
        font-size: 2rem;
    }
}