@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

a {
  text-decoration: none;
}

body {
  font-family: 'Quicksand', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #282c34;
  color: white;
  max-width: 1100px;
  margin: 0 auto;

}

a {
  color: #f8ecd5;
}


.App{padding:20px;text-align:left}.won-match{background-color:#49b67a}.lost-match{background-color:#B6494E}

.MatchDetailCard{display:grid;grid-template-columns:1fr 1fr;padding:10px;margin-bottom:20px}.MatchDetailCard .match-date,.MatchDetailCard .match-venue,.MatchDetailCard .match-result{margin-top:10px}.MatchDetailCard .addn-detail{text-align:right}.MatchDetailCard .addn-detail>p{margin-bottom:20px}

.MatchSmallCard{padding:10px;margin-bottom:20px}.MatchSmallCard .match-result{margin-top:10px}

.TeamPage{display:grid;grid-template-columns:1fr 1fr 1fr 1fr;grid-template-rows:1fr 1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.TeamPage .team-name{grid-column:span 3;justify-self:start;align-self:end}.TeamPage .team-name-disp{font-size:4rem;margin-bottom:20px}.TeamPage .win-loss{justify-self:center;font-weight:bold;text-align:center;padding:20px}.TeamPage .match-detail{grid-column:span 4}.TeamPage .more-link{-webkit-align-self:center;align-self:center;justify-self:center;font-size:2rem}

.YearSelector{text-align:right;list-style-type:none}.YearSelector li{margin-top:10px;margin-bottom:10px}

.MatchPage{display:grid;grid-template-columns:1fr 6fr;grid-column-gap:20px}.MatchPage .year{justify-self:end;padding:20px}.MatchPage .pg-header{margin:20px auto}

.HomePage .team-grid{display:grid;grid-template-columns:1fr 1fr;grid-column-gap:10px;grid-row-gap:10px}.HomePage .header-section{justify-self:center;-webkit-align-self:center;align-self:center;padding:20px;text-align:center;font-size:1.5rem}

.TeamTile{height:150px;text-align:center;border:1px solid white;padding:20px}

